import React, { Component } from "react";
import CourseDataService from "../course.service";
import { useState, useEffect } from 'react';
import { Button, Form, Container, Header, Input } from 'semantic-ui-react';
import { Link } from 'gatsby';
import axios from 'axios';
import "../css/evalForm.css";
import { stripBasename } from 'history/PathUtils';
import { createBrowserHistory } from "history";
import '../css/addcourse.css';

export default class AddCourse extends Component {
    constructor(props) {
        super(props);
        this.onChangeId = this.onChangeId.bind(this);
        this.onChangeName = this.onChangeName.bind(this);
        this.onChangeTerm = this.onChangeTerm.bind(this);
        this.onChangeYear = this.onChangeYear.bind(this);
        this.onChangeProf = this.onChangeProf.bind(this);
        this.onChangeStart = this.onChangeStart.bind(this);
        this.onChangeEnd = this.onChangeEnd.bind(this);
        this.onChangeDay = this.onChangeDay.bind(this);
        this.saveCourse = this.saveCourse.bind(this);
        this.newCourse = this.newCourse.bind(this);

        this.state = {
            Course_id: "",
            Course_name: "",
            Course_term: "",
            Course_year: "",
            Prof_id: "",
            Course_start: "",
            Course_end: "",
            Course_day: "",

            submitted: false
        };
    }

    onChangeId(e) {
        this.setState({
            Course_id: e.target.value
        });
    }

    onChangeName(e) {
        this.setState({
            Course_name: e.target.value
        });
    }

    onChangeTerm(e) {
        this.setState({
            Course_term: e.target.value
        });
    }

    onChangeYear(e) {
        this.setState({
            Course_year: e.target.value
        });
    }

    onChangeProf(e) {
        this.setState({
            Prof_id: e.target.value
        });
    }

    onChangeStart(e) {
        this.setState({
            Course_start: e.target.value
        });
    }

    onChangeEnd(e) {
        this.setState({
            Course_end: e.target.value
        });
    }

    onChangeDay(e) {
        this.setState({
            Course_day: e.target.value
        });
    }

    saveCourse() {
        var data = {
            Course_id: this.state.Course_id,
            Course_name: this.state.Course_name,
            Course_term: this.state.Course_term,
            Course_year: this.state.Course_year,
            Prof_id: this.state.Prof_id,
            Course_start: this.state.Course_start,
            Course_end: this.state.Course_end,
            Course_day: this.state.Course_day
        };

        CourseDataService.create(data)
            .then(response => {
                this.setState({
                    Course_id: response.data.Course_id,
                    Course_name: response.data.Course_name,
                    Course_term: response.data.course_term,
                    Course_year: response.data.course_year,
                    Prof_id: response.data.Prof_id,
                    Course_start: response.data.Course_start,
                    Course_end: response.data.Course_end,
                    Course_day: response.data.Course_day,

                    submitted: true
                });
                console.log(response.data);
            })
            .catch(e => {
                console.log(e);
            });
    }

    newCourse() {
        this.setState({
            Course_id: "",
            Course_name: "",
            Course_term: "",
            Course_year: "",
            Prof_id: "",
            Course_start: "",
            Course_end: "",
            Course_day: "",

            submitted: false
        });
    }

    render() {
        return (
            <div className="submit-form">
                {this.state.submitted ? (
                    <div>
                        <h4>You submitted succesfully!</h4>
                        <button className="btn btn-success" onClick={this.newCourse}>
                            Submit Another Course
                        </button>
                        <Button variant="light"><Link to="/professoraddview">Back to Menu</Link></Button>
                    </div>
                ) : (
                        <div class="container">
                            <Container fluid className="container">
                                <Header as="h3">Please fill out the following information for the new course.</Header>
                                <div></div><br />

                                <Form className="form">
                                    <Form.Field inline>
                                        <label>Course ID  </label>
                                        <textarea id="Course_id" name="Course_id" rows="1" cols="50" value={this.state.course_id} onChange={this.onChangeId}></textarea>
                                    </Form.Field>
                                    <div></div><br />

                                    <Form.Field inline>
                                        <label>Course Name  </label>
                                        <textarea id="Course_name" name="Course_name" rows="1" cols="50" value={this.state.Course_name} onChange={this.onChangeName}></textarea>
                                    </Form.Field>
                                    <div></div><br />

                                    <Form.Field inline>
                                        <label>Course Term  </label>
                                        <select name="Course_term" id="Course_term" value={this.state.Course_term} onChange={this.onChangeTerm}>
                                            <option value="default" selected="true">Choose a term.</option>
                                            <option>1</option>
                                            <option>2</option>
                                            <option>3A</option>
                                            <option>3B</option>
                                        </select>
                                    </Form.Field>
                                    <div></div><br />

                                    <Form.Field inline>
                                        <label>Course Year</label>
                                        <textarea id="Course_year" name="Course_year" rows="1" cols="50" value={this.state.Course_year} onChange={this.onChangeYear}></textarea>
                                    </Form.Field>
                                    <div></div><br />

                                    <Form.Field inline>
                                        <label>Start Time  </label>
                                        <input type="time" id="Course_start" name="Course_Start" min="8:00" max="20:00" required value={this.state.Course_start} onChange={this.onChangeStart}></input>
                                    </Form.Field>
                                    <div></div><br />

                                    <Form.Field inline>
                                        <label>End Time  </label>
                                        <input type="time" id="Course_end" name="Course_End" min="8:00" max="20:00" required value={this.state.Course_end} onChange={this.onChangeEnd}></input>
                                    </Form.Field>
                                    <div></div><br />

                                    <Form.Field inline>
                                        <label>Course Day(s)  </label>
                                        <select name="Course_day" id="Course_Day" value={this.state.course_day} onChange={this.onChangeDay}>
                                            <option value="default" selected="true">Select the day(s).</option>
                                            <option>Monday/Wednesday/Friday</option>
                                            <option>Monday/Wednesday</option>
                                            <option>Monday</option>
                                            <option>Wednesday</option>
                                            <option>Tuesday/Thursday</option>
                                            <option>Tuesday</option>
                                            <option>Thurday</option>
                                        </select>
                                    </Form.Field>
                                    <div></div><br />

                                    <Button color="black" type='submit' onClick={this.saveCourse}>Add Course</Button>
                                </Form>
                            </Container>
                        </div>
                    )}
            </div>
        );
    }
}